.btn {
  border-radius: 15px;
  color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.btnPrimary {
  background-color: rgb(85, 88, 237) !important;
}

.btnSecondary {
  background-color: rgb(80, 82, 84);
}

.btnSmall {
  font-size: 14px;
}

.btnMedium {
  font-size: 18px;
}